.nopage-flex {
    margin-top: 150px;
    width: 100%;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}

.err404 {
    color: #FF6060;
    font-size: 288px;
    font-family: 'Arial', 'Montserrat', 'serif';
    font-weight: 700;

}

.sorry {
    font-size: 36px;
    color: #FF6060;
}

.return-home {
    font-size: 18px;
    color: #FF6060;
    margin-top: 182px;
    margin-bottom: 100px;
}

@media all and (max-width : 1023px) {
    .nopage-flex {
        width: 100%;
    }

    .err404 {
        font-size: 96px;
    }

    .sorry {
        font-size: 18px;
        text-align: center;
    }

    .return-home {
        font-size: 14px;
    }
}
 
